/*-----------------------------------------------------------------------------------*/
/*  Menu Header Logo and Menu
/*-----------------------------------------------------------------------------------*/
#header-top {
  position: relative;
}
/*-----------------------------------------------------------------------------------*/
/*  DECLARATE 
/*-----------------------------------------------------------------------------------*/
*,
*:before,
*:after {
  transition: all 0.5;
  text-rendering: optimizeLegibility;
  box-sizing: border-box;
  line-height: 1.875rem;
  font-family: "Avenir-Roman", sans-serif;
}
@media (max-width: 768px) {
  * *,
*:before *,
*:after * {
    line-height: initial !important;
  }
}

html,
body {
  padding: 0px;
  margin: 0px;
  scroll-behavior: smooth;
  background-color: white;
}

body {
  margin: 0;
  padding: 0;
  position: relative;
}

footer h3 {
  color: white !important;
}
footer ul li {
  margin: 0px;
}

@media (max-width: 768px) {
  ul {
    padding-left: 2rem !important;
  }
}
ul li {
  line-height: 1.5rem;
  margin-bottom: 1.5rem;
}

.shadow-top_out {
  position: relative;
}
.shadow-top_out::before {
  content: "";
  position: absolute;
  min-width: 100%;
  background: #545454;
  background: linear-gradient(0deg, #ebebeb 0%, rgba(205, 205, 205, 0) 40%);
  height: 40px;
