/*------------------------------------------*/
/*   faq
/*------------------------------------------*/
.faq .drop-list__item {
  cursor: pointer;
}
.faq ul {
  min-width: 100%;
}
.faq ul li {
  position: relative;
  color: #0a0a0a;
  line-height: 34px;
  transition-property: color;
  transition-duration: 0.2s;
  margin-bottom: auto;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 2rem 0px;
}
@media (max-width: 768px) {
  .faq ul li {
    font-weight: 500;
    font-size: 20px;
  }
}
.faq ul li .question:after {
  content: "+";
  position: absolute;
  right: 0px;
  transition: all 0.2s;
  font-size: 3rem;
  font-weight: normal;
}
.faq ul li .question:hover {
  color: #664fb7;
}
.faq ul li .question:hover:after {
  transform: rotate(90deg);
  font-size: 3.1rem;
}
.faq ul li .text {
  font-size: 15px;
  line-height: 28px;
  color: #000;
  font-weight: lighter;
  overflow: hidden;
  height: 0px;
  transition: all 0.5s;
}
.faq ul li.active {
  transition: all 0.5s;
}
.faq ul li.active .text {
  margin-top: 2rem;
}
.faq ul li.active.hover .question:after {
  content: "-";
  transform: rotate(0deg);
}
.faq ul li.active .question:after {
  content: "-";
  transform: rotate(0deg);
}
.faq ul li.active .text {
  transition: all 0.5s;
  height: auto !important;
}
.faq .drop-list__item {
  border-bottom: 2px solid #0a0a0a;
  min-width: 100%;
}

