/*-----------------------------------------------------------------------------------*/
/*  animate
/*-----------------------------------------------------------------------------------*/
.animate {
  opacity: 0;
  transition: all 0.5s;
  transform: translateX(-20%) scale(0.9);
}
.animate.active {
  opacity: 1;
  transform: translateX(0%) scale(1);
}

@keyframes pulse {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/*------------------------------------------*/
/*   SLider Logos
/*------------------------------------------*/
.slick-prev,
.slick-next {
  width: 58px !important;
  height: 58px;
  top: 10px;
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  background-color: #fff;
  border-radius: 50px;
  border: 0;
  outline: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.18);
  transform: translateY(0);
  cursor: pointer;
  z-index: 10;
  opacity: 1;
  transition: all 0.3s;
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 768px) {
  .slick-prev,
.slick-next {
    display: none !important;
  }
}

.slick-next {
  bottom: 0;
  right: -20px;
  background-image: url("../../inc/images/arow-right.svg");
}

.slick-prev {
  bottom: 0;
  left: -20px;
  background-image: url("../../inc/images/arrow-left.svg");
}

